<template>
    <div class="sec-kill-title">
        <div class="title-left">
            <div class="title-name"></div>
            <strong>{{hour}}点场</strong>
            <div class="kill-time">
                <span class="hour">00</span>
                <span>:</span>
                <span class="minute">{{minute}}</span>
                <span>:</span>
                <span class="second">{{second}}</span>
            </div>
        </div>
        <div class="title-right">更多秒杀
            <span class="more">></span>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            time: new Date()
        };
    },
    created() {
        setInterval(() => {
            this.time = new Date();
        }, 1000);
    },
    computed: {
        hour: function() {
            return this.time.getHours();
        },
        minute: function() {
            let min = 59 - this.time.getMinutes();
            return min > 9 ? min : "0" + min;
        },
        second: function() {
            let sec = 59 - this.time.getSeconds();
            return sec > 9 ? sec : "0" + sec;
        }
    }
};
</script>

<style lang='less' scoped>
.sec-kill-title {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 8vw;
    align-items: center;
    background: url(data:image/png;base64,)
        no-repeat;
    background-size: contain;
    .title-left {
        display: flex;
        align-items: center;
        .title-name {
            height: 3vh;
            width: 20vw;
            margin-right: 1vw;
            background-size: contain;
            background-repeat: no-repeat;
            background-image: url(data:image/png;base64,);
        }
        strong {
            font-size: 3vw;
        }
        .kill-time {
            margin-left: 1vw;
            span {
                font-size: 2vw;
            }
        }
    }
    .title-right {
        font-size: 3vw;
        display: flex;
        align-items: center;
        color: #f23030;
        .more {
            display: inline-block;
            color: #fff;
            background-color: #f23030;
            border-radius: 100%;
            width: 3.5vw;
            height: 3.5vw;
            line-height: 3.5vw;
            margin-left: 1vw;
        }
    }
}
</style>